.formContainer {
    display: flex;
    flex-direction: column;
    text-align: left;
    background: linear-gradient(135deg, 
        rgba(41, 46, 66, 0.95) 0%,
        rgba(70, 55, 42, 0.95) 100%
    );
    border: 2px solid rgba(255, 140, 66, 0.3);
    border-radius: 20px;
    padding: 30px;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    min-width: 30%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.formContainer:hover {
    border-color: rgba(255, 140, 66, 0.6);
    box-shadow: 0 8px 16px rgba(255, 60, 130, 0.2);
    transform: translateY(-2px);
}

.formElement {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 15px;
    color: #e1e2ef;
}

.formElement input, textarea {
    padding: 12px;
    border-radius: 10px;
    border: 2px solid rgba(255, 140, 66, 0.3);
    background-color: rgba(41, 46, 66, 0.7);
    color: #e1e2ef;
    resize: none;
    transition: all 0.3s ease;
}

.formElement input:focus, textarea:focus {
    outline: none;
    border-color: rgba(255, 140, 66, 0.6);
    box-shadow: 0 0 8px rgba(255, 60, 130, 0.2);
}

.formButton {
    background: linear-gradient(135deg,
        rgba(255, 140, 66, 0.15) 0%,
        rgba(255, 60, 130, 0.15) 100%
    );
    width: 120px;
    padding: 12px;
    margin-left: auto;
    border-radius: 12.5px;
    font-weight: 600;
    border: 2px solid rgba(255, 140, 66, 0.3);
    color: #e1e2ef;
    cursor: pointer;
    transition: all 0.3s ease;
}

.formButton:hover {
    color: #FF8C42;
    background: linear-gradient(135deg,
        rgba(41, 46, 66, 0.9) 0%,
        rgba(70, 55, 42, 0.9) 100%
    );
    border-color: rgba(255, 140, 66, 0.6);
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}